.vas_floor{
    position: relative;
    list-style: none;
    width: 490px;
    background-color: #6c3c36;
    transform: skewY(-10deg);
    padding: 15px;
    border-bottom: solid rgb(245, 216, 180) 10px;
    height: 50px;
}

